        // let data = {
        //   orderId: '1815535340000152'
        // }
        self.$http.get("http://106.75.215.120:6769/submitOrder/pid=ABC&uname=inita&amount="+this.investAmount).then(res => {
          res = res.data
          console.log(res)
          if (res.Ret == '200' && res.Data) {
