        let self = this
        // let data = {
        //   orderId: '1815535340000152'
        // }
        self.$http.get("http://106.75.215.120:6768/GetQuoteInfo/projectName=ABC&investor=inita").then(res => {
          res = res.data
          console.log(res)
          if (res.Ret == '200' && res.Data) {
            //success
            if(res.Data.Code=='200'){
              this.accountInfo={
                  total:res.Data.Info.Quote,
                  miniBuy:res.Data.Info.Minimal,
                  account:res.Data.Info.Balance
              }
              this.tableData[0].progress=parseInt(res.Data.Info.Percentage)
              //  //test 
              // this.accountInfo={
              //   total:3000,
              //   miniBuy:1000,
              //   account:2000
              // }
              // this.tableData[0].progress=100
            }else {
