      getOrderInfo() {
        let self = this
        let data = {
          orderId: '1815535340000152'
        }
        self.$http.post("http://106.75.227.191:9001/order/getOrderInfo.do", data).then(res => {
          res = res.data
          if (res.dataChainCodeType == 'Success' && res.code == 1000) {
            //success
            console.log(res.data)
            this.orderDetail = res.data
            this.orderDetail.creationTime= new Date(this.orderDetail.creationTime).Format("yyyy-MM-dd hh:mm")
            this.orderStatusStr = this.formatter(this.form.orderStatus)
          } else {
