        }).catch(res => {
          // this.showMessage(res.msg, 'error', true, 2000);
        })
      },
      showMessage(msg, type, close, dura, end) {
        this.$message({
          message: msg,
          type: type,
          showClose: close,
          duration: dura
        }, end)
      },
  init() {
