<template>
  <div class="wrapper">
      <div class="relative">
        <navigation></navigation>
        <div class="content">
          <transition name="move" mode="out-in"><router-view></router-view></transition>
        </div>
      </div>
  </div>
</template>

<script>
  import navigation from './nav.vue';
  export default {
    data(){
      return{
        currPage:null
      }
    },
    components:{
      navigation
    },
    watch:{
      '$route' (to,from){
        this.currPage= this.$route.path;
        console.log(this.currPage);
      }
    }
  }
</script>
<style scoped>
  .relative{
    position: relative;
  }
  .content{
    width: 100%;
    padding-top: 1px;
    margin: auto;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }

</style>



