<template>
  <section class="wallet">
    <div class="banner">fafs</div>
    <el-row class="p-t-b-30">
      <el-col :span="12" :offset="6">
        <el-row class="font-c-f font26 font-w-600 textleft"></el-row>
        <template v-for="item in list">
        <el-row class="bg-theme item">
          <el-col :span="12" class="textleft">
            <el-row class="font-c-f font22">{{item.name}}</el-row>
            <el-row class="font-c-theme p-t-10 font22">{{item.total}}</el-row>
          </el-col>
          <el-col :span="12" class="textright">
            <el-row class="font-c-f font22">{{item.dongjiezhong}}</el-row>
            <el-row class="p-t-10" v-if="item.jiaoyi">
              <el-button class="bg-theme"></el-button>
              <el-button class="txbtn"></el-button>
            </el-row>
              <el-row v-else class="p-t-10">
                <el-button class="bg-theme"><router-link to="/home"></router-link></el-button>
              </el-row>
          </el-col>
        </el-row>
        </template>
      </el-col>
    </el-row>
  </section>
</template>

<script>
    export default {
        name: "wallet",
      data(){
          return {
            list:[
              {
                name:'USTD',
                total:'50000.0000',
                dongjiezhong:'20000.00',
                jiaoyi:true

              }, {
                name:'ABC',
                total:'50000.0000',
                dongjiezhong:'20000.00',
                jiaoyi:false

              }, {
                name:'DCT',
                total:'3000.0000',
                dongjiezhong:'200.00',
                jiaoyi:true

              }, {
                name:'BCY',
                total:'0.00',
                dongjiezhong:'0.00',
                jiaoyi:true

              }, {
                name:'JHK',
                total:'0.00',
                dongjiezhong:'0.00',
                jiaoyi:true

              }, {
                name:'LKH',
                total:'0.00',
                dongjiezhong:'0.00',
                jiaoyi:true

              }
            ]
          }
      }
    }
</script>

<style scoped>
  .banner{
    width: 100%;
    height: 300px;
    background: #121420;
  }
  a{
   color: #ffffff;
  }
  .p-t-10{
    padding-top: 10px;
  }
  .item{
    padding:15px 20px;
    margin-top: 20px;
  }
  .txbtn{
    background:#678b9d;
  }
</style>



