<template>
    <section class="nav">
        <el-row class="header-cont">
          <img class="logo" src="../../static/logo.png" height="40"/>
        </el-row>
        <el-row class="textcenter">
          <el-col :span="16" :offset="4" class="navi">
            <el-menu
              :default-active="activeIndex"
              mode="horizontal"
              text-color="#ffffff"
              active-text-color="#448eba"
              @select="handleSelect">
              <el-menu-item index="home">
                <router-link to="/">/Investment</router-link>
              </el-menu-item>
              <el-menu-item index="transaction" class="one">
                <router-link to="/transaction">/Transaction</router-link>
              </el-menu-item>
              <el-menu-item index="wallet">
                <router-link to="/wallet">/wallet</router-link>
              </el-menu-item>
            </el-menu>
          </el-col>
        </el-row>
    </section>
</template>
<script>
    export default {
        name: 'navigation',
        data() {
            return {
                activeIndex: 'home',
            }
        },
        methods: {
            // 
            handleSelect(key, keyPath) {
                this.activeIndex = keyPath[0];
            }
        }
    }
</script>
<style scoped>
  .nav{
    /*position: fixed;*/
    width: 100%;
    /*height: 100px;*/
    z-index: 999;
    left: 0;
    margin-top: 15px;
    right:0;
  }
.el-menu--horizontal>.el-menu-item a{
    padding: 0 20px;
    display: block;
}
.logo{
  position: relative;
  top:7px;
}
  .logotext{
    color: #fff;
    height: 65px;
    line-height: 65px;
    vertical-align: top;
  }
</style>



