import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import homeCN from '@/components/homeCN'
import index from '@/pages/index'
import indexCN from '@/pages/indexCN'
import transaction from '@/pages/transaction'//
import transactionCN from '@/pages/transactionCN'//
import wallet from '@/pages/wallet'//
import walletCN from '@/pages/walletCN'//
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      component: home,
      children:[
        {
          path: '/',
          name: 'index',
          component:index
        },{
          path: '/transaction',
          name: 'transaction',
          component:transaction
        },{
          path: '/wallet',
          name: 'wallet',
          component:wallet
        }
      ]
    }
  ]
})



